import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { MeshBasicMaterial, WebGLBufferRenderer } from 'three'
import { FirstPersonControls } from 'three/examples/jsm/controls/FirstPersonControls.js'
import * as dat from 'dat-gui'

/**
 * Base
 */
// Debug
const debugObject ={}
const gui = new dat.GUI({
    width: 400
})

// Textures
const textureLoader = new THREE.TextureLoader()
const cubeTextureLoader = new THREE.CubeTextureLoader()

const doorColorTexture = textureLoader.load('/textures/door/color.jpg')
const doorAlphaTexture = textureLoader.load('/textures/door/alpha.jpg')
const doorAmbientOcclusionTexture = textureLoader.load('/textures/door/ambientOcclusion.jpg')
const doorHeightTexture = textureLoader.load('/textures/door/height.jpg')
const doorNormalTexture = textureLoader.load('/textures/door/normal.jpg')
const doorMetalnessTexture = textureLoader.load('/textures/door/metalness.jpg')
const doorRoughnessTexture = textureLoader.load('/textures/door/roughness.jpg')
const matcapTexture = textureLoader.load('/textures/matcaps/8.png')
const gradientTexture = textureLoader.load('/textures/gradients/5.jpg')

const environmentMapTexture = cubeTextureLoader.load([
    '/textures/environmentMaps/4/px.png',
    '/textures/environmentMaps/4/nx.png',
    '/textures/environmentMaps/4/py.png',
    '/textures/environmentMaps/4/ny.png',
    '/textures/environmentMaps/4/pz.png',
    '/textures/environmentMaps/4/nz.png'
])

/**
 * Base
 */
// Canvas
const canvas = document.querySelector('canvas.webgl')

// Sizes
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

// Scene
const scene = new THREE.Scene()

// Debug Cube Color 1
debugObject.boxColor = '#252777'


// Items
// const box1 = new THREE.Mesh(
//     new THREE.BoxGeometry(1, 1, 1),
//     new THREE.MeshBasicMaterial({color: debugObject.boxColor})  
// )



// Create Objects
// const plane1 = new THREE.Mesh(
//     new THREE.BoxGeometry(10,0.1,10),
//     new THREE.MeshBasicMaterial({ color: 0xff00ff})
// )
// plane1.position.y=-0.5

// scene.add(plane1)

const sphere1 = new THREE.Mesh(
    new THREE.SphereGeometry(),
    new THREE.MeshBasicMaterial({ color: 0xff0000 })
)
sphere1.position.y=3
sphere1.position.x=3
scene.add(sphere1)

// scene.add(box1)
    
/**
 * Lights
 */
 const ambientLight = new THREE.AmbientLight(0xffffff, 0.5)
 scene.add(ambientLight)
 
 const directionalLight = new THREE.DirectionalLight(0xffffff, 0.6)
 directionalLight.castShadow = true
 directionalLight.shadow.mapSize.set(1024, 1024)
 directionalLight.shadow.camera.far = 15
 directionalLight.shadow.camera.left = - 7
 directionalLight.shadow.camera.top = 7
 directionalLight.shadow.camera.right = 7
 directionalLight.shadow.camera.bottom = - 7
 directionalLight.position.set(5, 5, 5)
 scene.add(directionalLight)

 const material = new THREE.MeshStandardMaterial()
 material.metalness = 1
 material.roughness = 0
 gui.add(material, 'metalness').min(0).max(1).step(0.0001)
 gui.add(material, 'roughness').min(0).max(1).step(0.0001)
 material.envMap = environmentMapTexture
 

 // Sphere
 const sphere = new THREE.Mesh(
     new THREE.SphereGeometry(0.5, 64, 64),
     material
 )
 sphere.geometry.setAttribute('uv2', new THREE.BufferAttribute(sphere.geometry.attributes.uv.array, 2))
 sphere.position.x = - 1.5
 

 // Back Plane
 const planeB = new THREE.Mesh(
     new THREE.PlaneGeometry(10, 10, 1, 10),
     material
 )
 planeB.geometry.setAttribute('uv2', new THREE.BufferAttribute(planeB.geometry.attributes.uv.array, 2))
 planeB.position.y=0
 planeB.position.x=0
 planeB.position.z=-5


 // Right Plane
 const planeR = new THREE.Mesh(
    new THREE.PlaneGeometry(10, 10, 1, 10),
    material
)
planeR.geometry.setAttribute('uv2', new THREE.BufferAttribute(planeR.geometry.attributes.uv.array, 2))
planeR.position.y=0
planeR.position.x=5
planeR.position.z=0
planeR.rotation.y= Math.PI * 1.5

// Left Plane
const planeL = new THREE.Mesh(
    new THREE.PlaneGeometry(10, 10, 1, 10),
    material
)
planeL.geometry.setAttribute('uv2', new THREE.BufferAttribute(planeL.geometry.attributes.uv.array, 2))
planeL.position.y=0
planeL.position.x=-5
planeL.position.z=0
planeL.rotation.y= Math.PI * -1.5

 // Floor Plane
const planeF = new THREE.Mesh(
    new THREE.PlaneGeometry(10, 10, 1, 10),
    material
)
planeF.geometry.setAttribute('uv2', new THREE.BufferAttribute(planeF.geometry.attributes.uv.array, 2))
planeF.position.y=-5
planeF.position.x=0
planeF.position.z=0
planeF.rotation.x= Math.PI * 1.5

 // Top Plane
 const planeT = new THREE.Mesh(
    new THREE.PlaneGeometry(10, 10, 1, 10),
    material
)
planeT.geometry.setAttribute('uv2', new THREE.BufferAttribute(planeT.geometry.attributes.uv.array, 2))
planeT.position.y=5
planeT.position.x=0
planeT.position.z=0
planeT.rotation.x= Math.PI * -1.5
 

// Torus
 const torus = new THREE.Mesh(
     new THREE.TorusGeometry(0.3, 0.2, 64, 128),
     material
 )
 torus.geometry.setAttribute('uv2', new THREE.BufferAttribute(torus.geometry.attributes.uv.array, 2))
 torus.position.x = 1.5

 // Add meshes
 scene.add(sphere, planeB, planeR, planeL, planeF, planeT, torus)
 
// Camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 1000)
camera.position.z = 3
scene.add(camera)

// Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)

// Debug Background Color
    debugObject.clearColor = '#252531'
    renderer.setClearColor(debugObject.clearColor)
    gui
        .addColor(debugObject, 'clearColor')
        .onChange(() =>
        {
        renderer.setClearColor(debugObject.clearColor)
        })

    // gui
    //     .addColor(debugObject, 'boxColor')
    //     .onChange(() =>
    //     {
    //     box1({color: debugObject.boxColor})  
    //     })
   

// FirstPersonControls
const fpControls = new FirstPersonControls(camera, renderer.domElement)
function animate() {
    requestAnimationFrame(animate)
    fpControls.update(1.0)
    renderer.render(scene, camera)
}
animate()
fpControls.lookSpeed = 0.001
// fpControls.mouseDragOn = true
fpControls.movementSpeed = 0.1

// Animate
const clock = new THREE.Clock()

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()

    // // Update controls
    // controls.update()

    // Box Rotation
    // box1.rotation.y = 1 * elapsedTime

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()